import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';
import cx from 'classnames';

import { Divider } from '../../../ui';
import './SkillsList.scss';

const Skill = ({ title, description, action, image }, index) => {
    const isOdd = index % 2 === 1;

    const containerClass = cx({
        'skill__container': true,
        'skill__container--odd': isOdd
    });

    return (
        <div className={containerClass}>
            <div className="skill__content"
                style={{order: isOdd ? 0 : 1}}>
                <h4>
                    {title}
                </h4>

                <p className="copytext-small">
                    {description}
                </p>
            </div>

            <div className="skill__image"
                style={{order: isOdd ? 1 : 0}}>
                <img src="./accounts.png" />
            </div>
        </div>
    );
};

const SkillsList = ({ params }) => {
    const count = params.length;

    const pairDivs = [];

    params.forEach((item, index) => {
        if (index % 2 === 1) {
            const pairDiv = (
                <div className="pair__container"
                    key={index}>
                    {Skill(params[index - 1], index - 1)}
                    <Divider />
                    {Skill(item, index)}
                </div>
            );

            pairDivs.push(pairDiv);
        }
    });

    return (
        <div className="skills-list__container">
            {pairDivs}
        </div>
    );
};

SkillsList.propTypes = {
    items: arrayOf(shape({
       title: string,
       description: string,
       action: string,
       actionUrl: string,
       image: string 
    }))
};

SkillsList.defaultProps = {
    items: []
};

export default SkillsList;



