import React from 'react';
import { string, node, func, bool } from 'react-proptypes';
import cx from 'classnames';

import './Input.scss';

const Input = ({ placeholder, type, name, required, autoFocus, small }) => {
    const cl = cx({
        'sh-input__container': true,
        'copytext-small': true,
        'sh-input__container--small': small
    });

    return (
        <input className={cl}
            placeholder={placeholder} 
            name={name} 
            type={type} 
            required={required} 
            autoFocus={autoFocus} />
    );
}

Input.propTypes = {
    placeholder: string,
    type: string,
    small: bool,
    name: string.isRequired
};

Input.defaultProps = {
    placeholder: '',
    type: 'text',
    small: false,
};

export default Input;



